@import "reset";
@import "variables";

* {
  box-sizing: border-box;
}
body {
  height: 100vh;
  font-family: RingsideExtraWide-Book,Blank,Helvetica Neue,Helvetica,sans-serif;
  background-color: white;
}
header {
  position: fixed;
  top: 0;
  left: 0;
  width: 39%;
  height: 100%;
  padding: 180px;
  padding: 10vh 0 72px 72px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: $red;
  h1 {
    font-size: 4.6rem;
    width: 50%;
    text-transform: uppercase;
    letter-spacing: 5px;
  }
  h3 {
    font-size: 23px;
    font-weight: 300;
    width: 60%;
    margin-bottom: 20px;
  }

  h3, p{
    line-height: 1.3;
  }
  p {
    width: 70%;
    text-align: justify;
    font-weight: 300;
    font-size: 17px;
    margin-bottom: 28px;
  };

  nav {
    width: 60%;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        cursor: pointer;
        margin-right: 12px;
        font-size: 14px;
        color: black;
        opacity: 0.5;
        padding-bottom: 5px;
        border-bottom: 2px solid rgba(0, 0, 0, 1);
        margin-bottom: 3px;
        &:hover {
          opacity: 1;
        }
      }
    }
  }
}

main {
  
  margin-left: $fixedWidth;
  .movie {
    background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.05) 2%,
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0),
    rgba(0, 0, 0, 0)
    ), $red;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    .wrapper {
      width: 80%;
      display: flex;
      flex-direction: column;
      .movie__header {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        margin-bottom: 25px;
        h4 {
          font-size: 32px;
          text-transform: uppercase;
        }
        h5 {
          letter-spacing: 2px;
        }
      }
    }
    img {
      width: 100%;
      box-shadow: 80px 80px -80px #000, 0 0 12px rgba(0, 0, 0, 0.06),
      inset 0 0 0 1px rgba(0, 0, 0, 0.2);
    }
  }
}